:root {
  --dur: 2s;
  --bg: #e3e4e8;
  --fg: #494a4e;
  font-size: calc(12px + (24 - 12) * (100vw - 320px) / (2560 - 320));
}

/* preloade css */

.loader {
  background: #e3e4e8;
  background: var(--bg);
  color: #494a4e;
  color: var(--fg);
  display: flex;
  justify-content: center;
  align-items: center;
  font: 1em/ 1.5 sans-serif;
  height: 100vh;
  overflow: hidden;
}
.preloader
{
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}
.preloader {
  margin: auto;
  position: relative;
  width: 12em;
  height: 24em;
}

.load img {
	width: 250px; height: 250px;
	background-size: contain;
	-webkit-animation: spin 5s linear infinite;
	        animation: spin 5s linear infinite;
}

@-webkit-keyframes spin {
	to { -webkit-transform: rotate(360deg); transform: rotate(360deg) }
}

@keyframes spin {
	to { -webkit-transform: rotate(360deg); transform: rotate(360deg) }
}

/* preloader css */

.player-content {
  display: none;
}

.playButton {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
}

.circle {
  display: block;
  position: absolute;
  top: -4px;
  left: -4px;
  width: 120px;
  height: 120px;
  stroke-dasharray: 1000px;
  stroke-dashoffset: 0;
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  transition: all 0.8s linear 0.2s, opacity 0s linear 0s;
}

.button-play {
  display: block;
  position: relative;
  border: 8px solid white;
  box-shadow: 0 4px 6px -3px rgba(0, 0, 0, 0.3),
    inset 0 3px 4px -2px rgba(0, 0, 0, 0.25);
  position: relative;
  width: 112px;
  height: 112px;
  background: #494a4e;
  background: var(--fg);

  border-radius: 50%;
  transition: all 0.2s cubic-bezier(0.68, -0.55, 0.465, 1.55) 0.2s,
    border-width 0.4s cubic-bezier(0.68, -0.55, 0.465, 1.55);
}
.button-play:before {
  display: block;
  position: absolute;
  content: "";
  bottom: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border: 30px solid transparent;
  border-width: 25px 0px 25px 40px;
  border-radius: 0;
  border-left-color: white;
  -webkit-transform: translate(-13px, 50%);
          transform: translate(-13px, 50%);
}
.button-play:hover,
.button-play.active {
  border: 4px solid white;
}
.button-play:hover .circle,
.button-play.active .circle {
  stroke-dasharray: -1000px;
  stroke-dashoffset: -500px;
  opacity: 0;
  transition: all 2s linear 0.3s, opacity 0.1s linear 2s;
}
.button-play:hover:before,
.button-play.active:before {
  -webkit-animation: playJump 0.5s linear infinite;
  animation: playJump 0.5s linear infinite;
}

@-webkit-keyframes playJump {
  0% {
    -webkit-transform: translate(-13px, 60%) scale(1.1, 0.7);
            transform: translate(-13px, 60%) scale(1.1, 0.7);
  }
  10% {
    -webkit-transform: translate(-13px, 30%) scale(0.9, 1.2);
            transform: translate(-13px, 30%) scale(0.9, 1.2);
  }
  20% {
    -webkit-transform: translate(-13px, 15%);
            transform: translate(-13px, 15%);
  }
  40% {
    -webkit-transform: translate(-13px, 10%);
            transform: translate(-13px, 10%);
  }
  50% {
    -webkit-transform: translate(-13px, 10%);
            transform: translate(-13px, 10%);
  }
  55% {
    -webkit-transform: translate(-13px, 18%);
            transform: translate(-13px, 18%);
  }
  60% {
    -webkit-transform: translate(-13px, 20%);
            transform: translate(-13px, 20%);
  }
  65% {
    -webkit-transform: translate(-13px, 25%);
            transform: translate(-13px, 25%);
  }
  70% {
    -webkit-transform: translate(-13px, 30%);
            transform: translate(-13px, 30%);
  }
  90% {
    -webkit-transform: translate(-13px, 60%) scale(1.2, 0.7);
            transform: translate(-13px, 60%) scale(1.2, 0.7);
  }
  100% {
    -webkit-transform: translate(-13px, 60%) scale(1.4, 0.7);
            transform: translate(-13px, 60%) scale(1.4, 0.7);
  }
}

@keyframes playJump {
  0% {
    -webkit-transform: translate(-13px, 60%) scale(1.1, 0.7);
            transform: translate(-13px, 60%) scale(1.1, 0.7);
  }
  10% {
    -webkit-transform: translate(-13px, 30%) scale(0.9, 1.2);
            transform: translate(-13px, 30%) scale(0.9, 1.2);
  }
  20% {
    -webkit-transform: translate(-13px, 15%);
            transform: translate(-13px, 15%);
  }
  40% {
    -webkit-transform: translate(-13px, 10%);
            transform: translate(-13px, 10%);
  }
  50% {
    -webkit-transform: translate(-13px, 10%);
            transform: translate(-13px, 10%);
  }
  55% {
    -webkit-transform: translate(-13px, 18%);
            transform: translate(-13px, 18%);
  }
  60% {
    -webkit-transform: translate(-13px, 20%);
            transform: translate(-13px, 20%);
  }
  65% {
    -webkit-transform: translate(-13px, 25%);
            transform: translate(-13px, 25%);
  }
  70% {
    -webkit-transform: translate(-13px, 30%);
            transform: translate(-13px, 30%);
  }
  90% {
    -webkit-transform: translate(-13px, 60%) scale(1.2, 0.7);
            transform: translate(-13px, 60%) scale(1.2, 0.7);
  }
  100% {
    -webkit-transform: translate(-13px, 60%) scale(1.4, 0.7);
            transform: translate(-13px, 60%) scale(1.4, 0.7);
  }
}

.videoplayer {
  position: absolute;
  z-index: 1;
  top: 90vh;
  width: 100vw;
  display: none;
  padding: 1rem;
  justify-content: center;
}

.videoscreen {
  -webkit-filter: blur(10px);
          filter: blur(10px);
}

audio {
  width: 80%;
  border-radius: 5px;
  display: flex;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.18);
  border-radius: 10px;
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
}

audio::-webkit-media-controls-enclosure {
  background: none;
}

audio::-webkit-media-controls-play-button {
  background-size: 50px;
}


