  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}
.toggle input:checked + span {
  background-color: #434ce8;
  background-color: var(--c-accent-primary);
}
.toggle input:checked + span:before {
  -webkit-transform: translateX(calc(100% - 2px));
